.Recipe {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.2em;
}

.Recipe h2 {
    font-size: 2.5rem;
}

.Recipe h3 {
    font-size: 1.5em;
}

.Recipe img {
    width: 30rem;
    margin: 1.5em;
}

#infoBox {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#infoBox p:first-of-type {
    font-size: 1.5rem;
    width: 80%;

}

#infoBox #time {
    margin-top: 1em;
    display: flex;
    align-items: center;
    color: rgb(100, 99, 99);
}

#prepBox {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#ingBox {
    background-color: rgb(36, 36, 36);
    border-radius: 10px;
    color: whitesmoke;
    padding: 2.5rem;
    margin: 2rem 1rem;

}


#ingBox li {
    list-style: none;
    margin: 1rem 0;
    font-weight: bold;
}


#instBox {
    background-color: whitesmoke;
    border: 2px solid black;
    border-radius: 10px;
    /* width: 50%; */
    margin: 2rem 1rem;
    padding: 2rem 3.5rem;
}

#instBox li {
    margin: 1rem 0;
}

#notes {
    margin: 2em;
}

#servings {
    display: flex;
    justify-content: center;
    align-items: center;
}

#servings p {
    margin-right: 1em;
}

#servings input {
    width: 3.5em;
    height: 2em;
    text-align: center;
    outline: none;
    font-size: 1rem;

}

#servings input:focus {
    border: 3px solid hotpink
}


@media only screen and (min-width: 600px) {
    #prepBox {
       flex-direction: row;
    }

    #instBox {
        width: 50%;
    }
}